import { createStore } from "redux"

const pageConfigs = {
  "e621": {
    "name": "e621",
    "display": "e621",
    "descriptors": ["furry", "nsfw"],
    "url": {
      "base": "https://e621.net",
      "post": "https://e621.net/posts.json",
      "params": {
        "tags": "tags",
        "page": "page",
        "limit": "limit"
      }
    },
    "posts": ["posts"],
    "file": {
      "post": ["id"],
      "preview": ["preview", "url"],
      "full": ["file", "url"]
    },
    "tags": {
      "general": ["tags", "general"],
      "character": ["tags", "character"],
      "copyright": ["tags", "copyright"],
      "artist": ["tags", "artist"],
      "meta": ["tags", "meta"],
      "other": [
        ["tags", "species"],
        ["tags", "invalid"],
        ["tags", "lore"]
      ]
    }
  },
  "danbooru": {
    "name": "danbooru",
    "display": "Danbooru",
    "descriptors": ["anime", "nsfw"],
    "url": {
      "base": "https://danbooru.donmai.us",
      "post": "https://danbooru.donmai.us/posts.json",
      "params": {
        "tags": "tags",
        "page": "page",
        "limit": "limit"
      }
    },
    "posts": [],
    "file": {
      "post": ["id"],
      "preview": ["preview_file_url"],
      "full": ["file_url"]
    },
    "tags": {
      "general": ["tag_string_general"],
      "character": ["tag_string_character"],
      "copyright": ["tag_string_copyright"],
      "artist": ["tag_string_artist"],
      "meta": ["tag_string_meta"],
      "other": []
    }
  },
}

const initialState = {
  posts: [],
  config: "e621",
  blacklist: []
}

function modifyStore(state = initialState, action) {
  switch (action.type) {
    case "config/modify":
      return { ...state, config: action.payload }
    case "posts/set":
      return { ...state, posts: action.payload }
    case "blacklist/set":
      return { ...state, blacklist: action.payload }
    default:
      return state
  }
}

const store = createStore(modifyStore)

export { pageConfigs, store }