import React from "react"

import { programConfig } from "../Config"
import { authorLink } from "../Helpers"

import Page from "../components/Page"

const About = () => {
  return (
    <Page>
      <h1>About</h1>
      <p>
        <strong>{programConfig.name}</strong> is a simple, easy to use alternative
        to most image gallery's UI. Made using React by {authorLink("github", programConfig.author.github)}.
      </p>
      <h3>To-do</h3>
      <ul>
        <li>Persistent search parameters.</li>
        <li>Add usage tab/section.</li>
        <li>Implement pagination feature to the home page.</li>
        <li>Add <code>order:</code>, <code>date:</code>, etc sliders to search box.</li>
        <li>Create a logo (and add <code>favicon.ico</code> / <code>logo192.png</code>).</li>
      </ul>
      <h3>v0.2.1</h3>
      <ul>
        <li>Added brand name to the navbar.</li>
        <li>Added a guide page on how to use the site.</li>
        <li>Improved code for posts page.</li>
        <li>...and other minor improvements.</li>
      </ul>
      <h3>v0.2.0</h3>
      <ul>
        <li>Implemented blacklist functionality.</li>
        <li>Improved code for cookies.</li>
        <li>Added toast notifications (top right).</li>
        <li>Added FontAwesome icons to the navbar.</li>
      </ul>
      <h3>v0.1.1</h3>
      <ul>
        <li>Renamed <code>Sites</code> page to <code>Source</code>.</li>
        <li>Updated search box on the front page to be more centered.</li>
      </ul>
      <h3>v0.1.0</h3>
      <ul>
        <li>Initial release.</li>
      </ul>
    </Page>
  )
}

export default About