import React from "react"

const getFromPath = (path, resp) => {
  let data = resp

  for (let i = 0; i < path.length; i++) {
    data = data[path[i]]
  }

  return data
}

const determineFileType = (file) => {
  let path = file.split(".")
  let ext = path[path.length - 1]

  let post_type = "none"

  if (["mp4", "webm"].includes(ext)) {
    post_type = "video"
  } else if (ext === "gif") {
    post_type = "gif"
  } else if (ext === "swf") {
    post_type = "flash"
  } else {
    post_type = "image"
  }

  return post_type
}

const capitalizeString = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1)
}

const authorLink = (service, user) => {
  let url = null

  switch (service) {
    case "github":
      url = "https://github.com/" + user
      break
    default:
      break
  }

  return (
    <span>
      <strong>
        <a href={url}>@{user}</a>
      </strong> on <strong>
        {capitalizeString(service)}
      </strong>
    </span>
  )
}

const fetchAllTags = (post, tags) => {
  let all_tag_paths = []

  Object.keys(tags).forEach((key, idx) => {
    if (key === "other") {
      for (let i = 0; i < tags[key].length; i++) {
        all_tag_paths.push(tags[key][i])
      }
    } else {
      all_tag_paths.push(tags[key])
    }
  })

  let all_tags = []

  for (let i = 0; i < all_tag_paths.length; i++) {
    let current_tags = getFromPath(all_tag_paths[i], post)

    if (typeof current_tags === "string") {
      all_tags = [...all_tags, ...current_tags.split(" ")]
    } else {
      all_tags = [...all_tags, ...current_tags]
    }
  }

  return all_tags
}

export {
  getFromPath,
  determineFileType,
  capitalizeString,
  authorLink,
  fetchAllTags
}