import React from "react"
import { Navbar, Nav } from "react-bootstrap"
import { Link } from "react-router-dom"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faHome, faBook, faTimes, faQuestion, faGlobe } from "@fortawesome/free-solid-svg-icons"

import { programConfig } from "../Config"

const Navigation = () => {
  return (
    <Navbar>
      <Navbar.Brand as={Link} to="/">{programConfig.name}</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Item>
            <Nav.Link as={Link} to="/">
              <FontAwesomeIcon icon={faHome} /> Home
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link as={Link} to="/source">
              <FontAwesomeIcon icon={faGlobe} /> Source
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link as={Link} to="/blacklist">
              <FontAwesomeIcon icon={faTimes} /> Blacklist
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link as={Link} to="/guide">
              <FontAwesomeIcon icon={faBook} /> Guide
            </Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link as={Link} to="/about">
              <FontAwesomeIcon icon={faQuestion} /> About
            </Nav.Link>
          </Nav.Item>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}

export default Navigation