import React from "react"

import { pageConfigs } from "../Store"
import Page from "../components/Page"

const Guide = () => {
  return (
    <Page>
      <h1>Guide</h1>
      <h3>Home</h3>
      <p>
        When you first go on the site you will be greeted with the home page.
        From here you will be able to search for posts using the various input
        boxes. Enter in a few phrases of what you want (for instance, <code>order:score
        rating:s date:month</code> will find you the highest ranked SFW posts in
        the last month for most image sites), then click the blue "search" button
        to find posts.
      </p>
      <h3>Source</h3>
      <p>
        On the source page you can change where the app gets posts from. Currently
        the site supports the following sources officially:
      </p>
      <ul>
        {Object.keys(pageConfigs).map((k, i) =>
          <li>{pageConfigs[k].display}</li>
        )}
      </ul>
      <p>
        Simply click on the card of the site you want to use, then head back to
        the home page and search. This value will be saved across site visits if
        you have cookies enabled.
      </p>
      <h3>Blacklist</h3>
      <p>
        If you want to block out certain posts from showing up you can block them
        based on the tags they have in the Blacklist tab. Simply type in the tags
        you want to block out and press the blue "add" button. These values will
        be saved across site visits if you have cookies enabled.
      </p>
    </Page>
  )
}

export default Guide