import React from "react"
import { useCookies } from "react-cookie"
import { Form, Button, ListGroup } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { ToastContainer, toast } from "react-toastify"

import { programConfig } from "../Config"

import Page from "../components/Page"

const Blacklist = () => {
  const dispatch = useDispatch()
  const blacklist = useSelector(store => store.blacklist)

  const [cookies, setCookie] = useCookies(["blacklist"])

  if (blacklist.length < 1) {
    if (cookies.blacklist !== undefined) {
      let init_cookies = cookies.blacklist.toString().split(programConfig.cookies.arraySeparator).filter(item => {
        return (item !== "" && item !== undefined)
      })

      if (init_cookies.length > 0) {
        dispatch({ type: "blacklist/set", payload: init_cookies })
      }
    }
  }

  const addToBlacklist = (e) => {
    e.preventDefault()

    let tags = e.target.tags.value.split(" ")
    let processed = []

    for (let i = 0; i < tags.length; i++) {
      if (tags[i] === "" || tags[i] === undefined) {
        continue
      }

      if (blacklist.includes(tags[i])) {
        toast.error(`'${tags[i]}' is already blacklisted.`, {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        })
      } else {
        processed.push(tags[i])
      }
    }

    let new_blacklist = [...blacklist, ...processed]

    dispatch({ type: "blacklist/set", payload: new_blacklist })
    setCookie("blacklist", new_blacklist.join(programConfig.cookies.arraySeparator), { path: "/" })

    e.target.tags.value = null
  }

  const removeFromBlacklist = (e) => {
    e.preventDefault()

    let new_blacklist = blacklist.filter(item => item !== e.target.innerHTML)

    dispatch({ type: "blacklist/set", payload: new_blacklist })
    setCookie("blacklist", new_blacklist.join(programConfig.cookies.arraySeparator), { path: "/" })
  }

  return (
    <Page>
      <h1>Blacklist</h1>
      <p>Choose which tags you want to block from your search. Separate multiple tags using a space, such as: <code>blocked_tag_one blocked_tag_two</code></p>
      <Form onSubmit={addToBlacklist} inline>
        <Form.Group controlId="formBlacklist">
          <Form.Control
            className="mb-2 mr-sm-2"
            type="text"
            name="tags"
            placeholder="Tags to blacklist..."
          />
          <Button variant="primary" type="submit" className="mb-2">
            Add
          </Button>
        </Form.Group>
      </Form>
      <ListGroup>
        {blacklist.map((item, idx) => (
          <ListGroup.Item variant="danger" key={idx} onClick={removeFromBlacklist}>
            {item}
          </ListGroup.Item>
        ))}
      </ListGroup>
      <ToastContainer/>
    </Page>
  )
}

export default Blacklist