import React, { useState } from "react"
import { Button, Form } from "react-bootstrap"

const Search = (props) => {
  const [state, setState] = useState({
    tags: "rating:s",
    page: 1,
    limit: 25
  })

  return (
    <div className="container mt-5 mb-5">
      <Form id="search-form" onSubmit={props.doSearch}>
        <Form.Group controlId="tags">
          <Form.Label>Tags</Form.Label>
          <Form.Control
            name="tags"
            type="text"
            value={state.tags}
            onChange={e => setState({ ...state, tags: e.target.value })}
          />
        </Form.Group>
        <Form.Group controlId="page">
          <Form.Label>Page</Form.Label>
          <Form.Control
            name="page"
            type="number"
            value={state.page}
            onChange={e => setState({ ...state, page: e.target.value })}
          />
        </Form.Group>
        <Form.Group controlId="limit">
          <Form.Label>Limit</Form.Label>
          <Form.Control
            name="limit"
            type="number"
            value={state.limit}
            onChange={e => setState({ ...state, limit: e.target.value })}
          />
        </Form.Group>
        <Button className="mt-2" variant="primary" type="submit">
          Search
        </Button>
      </Form>
    </div>
  )
}

export default Search