import React, { useState } from "react"
import { useDispatch, useSelector } from "react-redux"

import Post from "./Post"
import Search from "./Search"
import { pageConfigs } from "../Store"
import { getFromPath, fetchAllTags } from "../Helpers"

const Posts = (props) => {
  const dispatch = useDispatch()
  const [state, setState] = useState({
    error: null,
    hasSearched: false,
    isLoaded: false,
    blacklistedCount: 0
  })

  const items = useSelector(store => store.posts)
  const config = useSelector(store => store.config)
  const blacklist = useSelector(store => store.blacklist)

  const doSearch = (e) => {
    e.preventDefault()

    setState({
      ...state,
      hasSearched: true,
      isLoaded: false
    })

    let current_config = pageConfigs[config]

    fetch(current_config.url.post + "?" + new URLSearchParams({
      tags: e.target.tags.value,
      page: e.target.page.value,
      limit: e.target.limit.value
    }))
    .then(res => res.json())
    .then(
      (result) => {
        let posts = getFromPath(current_config.posts, result)
        let blacklist_count = 0

        let filtered = posts.filter(post => {
          let tags = fetchAllTags(post, current_config.tags)

          if (blacklist.some(item => tags.includes(item))) {
            blacklist_count++
            return false
          }

          return true
        })

        setState({
          ...state,
          isLoaded: true,
          error: false,
          blacklistedCount: blacklist_count
        })

        dispatch({ type: "posts/set", payload: filtered})
      },
      (error) => {
        setState({
          ...state,
          isLoaded: true,
          error: true
        })

        dispatch({ type: "posts/set", payload: [] })
      }
    )
  }

  const PostBox = (props) => {
    if (state.error) {
      return (
        <div className="error">
          Error: {state.error.message}
        </div>
      )
    } else if (!state.isLoaded && state.hasSearched) {
      return (
        <div className="loading">
            Loading...
        </div>
      )
    } else if (state.isLoaded && items.length < 1 && !state.error) {
      return (
        <div className="error">
          No posts were found. ({state.blacklistedCount} posts were blacklisted)
        </div>
      )
    } else if (state.isLoaded) {
      return (
        <div id="posts">
          {props.items.map(item => (
            <Post post={item}/>
          ))}
        </div>
      )
    } else {
      return (<div></div>)
    }
  }

  return (
    <div>
      <Search doSearch={doSearch} />
      <PostBox items={items} />
    </div>
  )
}

export default Posts